body,
html,
h1,
p {
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Rubik';
    color: white;
}

h1 {
    font-weight: 500;
}

.logo {
    width: 137px;
    height: 34px;
    display: block;
    margin: 0 auto;
    padding-top: 40px;
    padding-bottom: 15px;
    background-position: center;
    background-image: url(images/logo.svg);
    background-repeat: no-repeat;
    background-size: contain;
}

.animation {
    position: fixed;
    top: 0;
    min-height: 100vh;
    width: 100%;
    height: 100%;
}

.circle {
    position: relative;
    background-image: url(images/circle.svg);
    background-repeat: no-repeat;
    background-size: contain;
}

.avatar {
    position: absolute;
    top: 6.5%;
    left: 6.5%;
    width: calc(100% - 16%);
    height: calc(100% - 16%);
    border-radius: 50%;
}

.avatar--empty {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 500;
    font-size: 40px;
    background: linear-gradient(50.62deg, #0085FF 12.57%, #0C26AE 86.74%);
}

.start {
    width: 300px;
    height: 107px;
    margin: 40px -30px 0 0;
    background-image: url(images/start.svg);
    background-repeat: no-repeat;
    background-size: contain;
}

.home {
    min-height: 100vh;
    background: linear-gradient(50.14deg, #3300FF 8.11%, #00C2FF 91.07%);
}

.home_content {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
}

.home::after {
    content: '';
    position: absolute;
    z-index: 0;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-image: url(images/background.svg);
    background-repeat: no-repeat;
    background-size: cover;
}

.home h1 {
    font-size: 48px;
}

.home p {
    display: block;
    width: 70%;
    margin-top: 10px;
    text-align: center;
    font-size: 30px;
}

.home_circles {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-top: 30px;
}

.home_circles span {
    margin: 0 15px;
    font-size: 40px;
}

.home_circle {
    width: 120px;
    height: 120px;
}

.hidden {
    opacity: 0;
}
